  margin-bottom: 25px;
}

.input-slider-group {
  height: 30px;
  width: 400px;
  margin: auto;
}

.custom-range {
  width: 60%;
}

.least-squares-container {
  margin-left: -100px;
  margin-right: 0;
}

.btn-selected {
  background-color: #4CAF50;
  color: white;
  border: 1px solid gray;
}

.btn-unselected {
  background-color: #555555;
  color: white;
  border: 1px solid gray;
}

h1 {
  font-size: 2em;
  padding: 1em 0;
}
h2 {
  font-size: 1.6em;
  padding: 0.5em 0;
}


.app {
  padding: 20px;
