}

.highcharts-navigator-xaxis.highcharts-axis-labels {
  fill: #666;
}

.highcharts-navigator-yaxis .highcharts-grid-line {
  stroke-width: 0;
}

.highcharts-scrollbar-thumb {
  fill: #606063;
  stroke: #606063;
